#add-proverb-button {
  background-color: #111;
}

#add-proverb-button:disabled,
#add-proverb-button:disabled:hover,
#add-proverb-button:disabled:focus,
#add-proverb-button:disabled:active,
#add-proverb-button[disabled] {
  background-color: #111;
  border-radius: 3px;
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.15);
  color: #fff;
  opacity: 0.5;
}
