.search-form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 !important;
  padding: 0;
  opacity: 0;
  width: 0px;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
  -webkit-transition: width 0.4s ease-in-out;
  -o-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}
.search-form-container.active {
  opacity: 1;
  width: 100%;
}

.search-icon-container {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.search-icon-container svg {
  filter: invert(0.2);
  transition: all 0.15s ease-in;
}
.search-icon-container svg:hover {
  -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
  filter: drop-shadow(3px 3px 8px rgba(0, 0, 0, 0.7)) invert(0.5) sepia(0.5)
    saturate(1) brightness(0.1);
  transform: scale(1.5);
}

.search-bar-container {
  opacity: 1;
  z-index: 1;
  padding: 0;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}
.search-bar {
  box-shadow: none !important;
  border-radius: 0;
  border: 0;
  outline: 0 !important;
  font-size: 20px;
  padding: 10px 20px 10px 0;
  height: 100%;
  background-color: transparent !important;
  color: #333;
  font-weight: 700;
  margin-top: 0 !important;
  font-family: "Poppins", sans-serif;
  letter-spacing: 2px;
}
.search-form-container.active .search-bar {
  border-bottom: 2px solid #f5f5f5;
}

.search-result-label {
  color: #111;
  background: #f5f5f5;
  border-radius: 5px;
  padding: 16px;
}
.search-fields {
  margin: -2rem auto;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}

@media all and (max-width: 600px) {
  .search-form-container {
    font-size: 0px;
    padding: 0;
    margin: 0;
  }
  .search-result-label {
    color: #111;
    background: #f5f5f5;
    border-radius: 5px;
    padding: 10px;
  }
  .search-fields {
    margin: -1rem auto;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
  }
  .search-fields .form-group label {
    margin-bottom: 0.5rem;
    font-size: 10px;
    text-align: center;
  }
}

@media all and (max-width: 450px) {
  .search-bar::placeholder {
    font-size: 1rem;
  }
}

@media all and (max-width: 300px) {
  .search-bar::placeholder {
    font-size: .8rem;
  }
}
